<template>
<div class="sidemenu">
    <div class="menu">
        <div class="closebtn" @click="closesidemenu">
            <i class="ri-close-line"></i>
        </div>
        <nav>
            <router-link :to="{
                name: 'Home',
                params:{hostid:hostid}
            }">
                <li @click="closemenu">首頁</li>
            </router-link>
            <router-link :to="{
                name: 'Eventlistpage',
                params:{hostid:hostid}
            }">
                <li @click="closemenu">搜尋活動</li>
            </router-link>

            <router-link :to="{
                name:'Userinfo',
                params:{hostid:this.hostid}
            }">
                <li @click="closemenu('profile')">個人資料</li>
            </router-link>

            <li>我的活動</li>

            <router-link :to="{
                name:'alreadysignup',
                params:{hostid:this.hostid}
            }">
                <li @click="closemenu('myact')" class="second-navitem">已報名活動</li>
            </router-link>
        </nav>
    </div>
</div>
</template>

<script>
export default {
    props: ['alert'],
    data() {
        return {

        }
    },
    mounted() {
        if (window.sessionStorage.getItem('storehostid')) {
            var storehostid = window.sessionStorage.getItem('storehostid');
            this.$store.commit('sethostid', JSON.parse(storehostid))
        }
        // anime({
        //     targets: '.menu',
        //     translateX: [200,0]
        // });
    },
    methods: {
        closesidemenu() {

            this.$store.dispatch('sidemenuani', 'close')

        },
        closemenu(val) {
            console.log(val)
            if (val == 'profile') {
                this.$store.commit('setprofilecurrentpage', 'profile')
            } else if (val == 'myact') {
                this.$store.commit('setprofilecurrentpage', 'myact')
            }

            if (this.alert == 'cancel') {
                alert('將取消此次變更')
            }
            this.$store.commit("setshowsidemenu", false);
        }
    },
    computed: {
        hostid() {
            return this.$store.state.hostid
        }
    }
}
</script>

