<template>
  <div class="actblock" @click="toeventpage" :style="{marginRight:margin+'px'}">
    <div class="actblock-img" :style="{backgroundImage: 'url(' +pic+')'}"></div>
    <div class="actblock-content">
      <h4 class="content-title">{{name}}</h4>
      <div class="type" :style="{borderColor:maincolor,color:maincolor}">{{type}}</div>
      <div class="content-info">
        <div class="date">{{date}}</div>
        <div class="time">{{time}}</div>
      </div>
      <div class="content-info">
        <div class="date">
          <span :style="{color:maincolor}" v-show="isdeadline==false">報名截止日：</span><span v-show="isdeadline==false">{{deadlinedate}}</span>
           <span :style="{color:maincolor}" v-show="isdeadline==true">報名截止日：已截止</span>
         
        </div>
      </div>
      <div class="price" :style="{color:maincolor}" v-show="fee!=''">NTD.{{fee}}</div>
      <div class="price" :style="{color:maincolor}" v-show="fee==''">NTD.0</div>
      
      <div class="number" v-if="number==0">
        <span class="maxnumber" :style="{backgroundColor:maincolor}">已額滿</span>
        </div>
        <div class="number" :style="{color:maincolor}" v-else>剩餘人數：{{number}}</div>
    </div>
    <!-- <div class="actblock-btn">
        <h3 class="fee" v-if="fee!=''">{{fee}}元/人</h3>
        <h3 class="fee" v-else>0元/人</h3>
        <fillbtn btnname="立即報名" @clickhandler="toeventpage"></fillbtn>
    </div>-->
  </div>
</template>

<script>
// import fillbtn from '../components/fillbtn'
export default {
  props: [
    "name",
    "loc",
    "date",
    "time",
    "number",
    "deadlinedate",
    "fee",
    "eventid",
    "pic",
    "type",
    "margin"
  ],
  data() {
    return {
      id: this.eventid,
      
      isdeadline:false
    };
  },
  mounted() {
    var today = new Date();
    let nowdays = new Date(
      today.getFullYear() + "/" + (today.getMonth() + 1) + "/" + today.getDate()
    );
    let actdate = new Date(this.deadlinedate);
    let minusdays = parseInt((actdate - nowdays) / 1000 / 60 / 60 / 24);
    console.log(minusdays)
    if (minusdays <= 0) {
      this.isdeadline = true
      // console.log('超過了');
    }
  },
  methods: {
    toeventpage() {
      this.$router.push({
        name: "Eventpage",
        params: {
          id: this.id
        }
      });
    }
  },
  computed: {
    hostinfo() {
      return this.$store.state.hostinfo[0];
    },
    maincolor(){
      return this.$store.state.maincolor
    }
  },
  components: {
    // fillbtn
  }
};
</script>

