  data() {
    return {
      emitarr: []
    };
  },
  components: {
    signupinfoblock,
    fillbtn,
    outlinebtn
  },
  methods: {
    topaymentinfo() {
      console.log("有點到");
      this.$store.commit("addsignupnumber");
      this.$store.commit("setpostsignupinfo", this.emitarr);
    },

    toeventpage() {
      this.$router.push({
        name: "Eventpage",
        params: {
          id: this.$route.params.id
        }
      });

      // this.$store.commit('minussignupnumber');
    },
    catchval(val) {
      console.log("有", val);
      this.emitarr = val;
      // this.$emit('test2',val)
    }
  },
  computed: {
    ticketnumber() {
      return this.$store.state.ticketnumber;
    },
    maincolor() {
      return this.$store.state.maincolor;
    }
  }
};
</script>

