<template>
<div class="profilepage">
    <Headerbar></Headerbar>
    <div class="bgp"></div>
    <div class="userpic">
        <div class="pic" :style="{backgroundImage:'url('+userdata.pictureUrl+')'}"></div>

    </div>
    <div class="switchtab ">

        <div class="myact" @click="switchtab('myact')" :class="{notcurrent:profilecurrentpage!='myact',current:profilecurrentpage=='myact'}">
            <router-link :to="{
                name:'alreadysignup',
                params:{hostid:this.hostid}
            }">
                <i class="ri-coupon-2-fill"></i>
                <span>我的活動</span>
            </router-link>
        </div>

        <div class="profile" @click="switchtab('profile')" :class="{notcurrent:profilecurrentpage!='profile',current:profilecurrentpage=='profile'}">
            <router-link :to="{
                name:'Userinfo',
                params:{hostid:this.hostid}
            }">

                <i class="ri-user-fill"></i>
                <span>個人資料</span>
            </router-link>
        </div>

    </div>
    <router-view></router-view>
    <Sidemenu v-show="showsidemenu"></Sidemenu>
</div>
</template>

<script>
import Headerbar from '../components/Header'
// import Profilecontent from '../components/Profilecontent'
import Sidemenu from '../components/Sidemenu'
export default {
    data() {
        return {
            currentstatus: 'profile',

        }
    },
    components: {
        Headerbar,
        Sidemenu
    },
    created() {

    },
    mounted() {
        this.$store.commit('sethostid', this.$route.params.hostid)
    },
    methods: {
        switchtab(val) {
            if (val == 'profile') {
                this.$store.commit('setprofilecurrentpage', 'profile')
            } else {
                this.$store.commit('setprofilecurrentpage', 'myact')
            }
        }
    },
    computed: {

        profilecurrentpage() {
            return this.$store.state.profilecurrentpage;
        },
        userdata() {
            return this.$store.state.userprofiledata;
        },
        showsidemenu() {
            return this.$store.state.showsidemenu
        },
        hostid() {
            return this.$store.state.hostid
        }
    }
}
</script>

