<template>
  <div class="signupinfo">
    <div class="process">
      <div class="process-signup">
        <div class="number" :style="{backgroundColor:maincolor,borderColor:maincolor}">1</div>
        <span class="payment" :style="{color:maincolor}">報名資訊</span>
      </div>
      <i :style="{color:maincolor}" class="ri-arrow-right-s-line"></i>
      <div class="process-signup">
        <div class="number" :style="{borderColor:maincolor,backgroundColor:maincolor}">2</div>
        <span :style="{color:maincolor}">付款資訊</span>
      </div>
      <i :style="{color:maincolor}" class="ri-arrow-right-s-line"></i>
      <div class="process-signup">
        <div class="number" :style="{borderColor:maincolor,color:maincolor}">3</div>
        <span class="submit" :style="{color:maincolor}">送出報名</span>
      </div>
    </div>
    <div class="paymentinfo">
      <h3 v-show="eventinfo[12]=='否'">此活動為免費活動</h3>
      <div class="paymentmethods" v-show="eventinfo[12]=='是'">
        <div class="price" :style="{color:maincolor}">總金額：{{eventinfo[13]}} 元</div>付款方式：
        <h5 v-for="(item,index) of paymentmenthods" :key="index">
          {{item}}
          <h5 v-show="item=='匯款'">帳號：{{eventinfo[15]}}</h5>
        </h5>
      </div>
    </div>
    <div class="btnwrap">
      <fillbtn btnname="上一步" width="147" height="43" @clickhandler="signupinfo"></fillbtn>
      <fillbtn btnname="下一步" width="147" height="43" @clickhandler="tochecksignupinfo"></fillbtn>
    </div>
  </div>
</template>

<script>
import fillbtn from "../components/fillbtn";
export default {
  data() {
    return {};
  },
  components: {
    fillbtn
  },
  methods: {
    signupinfo() {
      this.$store.commit("minussignupnumber");
    },
    tochecksignupinfo() {
      this.$store.commit("addsignupnumber");
    }
  },
  computed: {
    eventinfo() {
      return this.$store.state.signupinfo;
    },
    paymentmenthods() {
      if (this.eventinfo[12] == "是") {
        return this.$store.state.signupinfo[14].split("、");
      } else {
        return null;
      }
    },
    maincolor() {
      return this.$store.state.maincolor;
    }
  }
};
</script>

