<template>
  <div class="signup">
    <Header></Header>
    <div class="Carousel" :style="{backgroundImage: 'url(' +eventinfo[1]+')'}"></div>
    <div class="actinfo" v-show="signupprogressnum<4">
      <div class="actinfo-name">
        <h4>{{eventinfo[0]}}</h4>
      </div>
    </div>
    <div class="content-info" v-show="signupprogressnum<4">
      <div class="loc">
        <div class="loc-icon" >
          <i class="ri-map-pin-line" :style="{color:maincolor}"></i>
        </div>
        <span class="decoline"></span>
        <span class="loc-content">{{eventinfo[4]}}</span>
      </div>
      <div class="loc">
        <div class="loc-icon">
          <i class="ri-time-line" :style="{color:maincolor}"></i>
        </div>
        <span class="decoline"></span>
        <span class="loc-content">
          {{eventinfo[2]}} {{eventinfo[3]}}
        </span>
      </div>
      <div class="loc">
        <div class="loc-icon">
          <i class="ri-group-2-line" :style="{color:maincolor}"></i>
        </div>
        <span class="decoline"></span>
        <span class="loc-content">{{eventinfo[7]}}人</span>
      </div>
    </div>
    <!-- <Choseevent v-show="signupprogressnum==0"></Choseevent> -->
    <Signupinfo v-show="signupprogressnum==0"></Signupinfo>
    <Paymentinfo v-show="signupprogressnum==1"></Paymentinfo>
    <Checksignupinfo v-show="signupprogressnum==2"></Checksignupinfo>
    <Successpage v-show="signupprogressnum==3"></Successpage>
    <Sidemenu v-show="showsidemenu" alert="cancel"></Sidemenu>
  </div>
</template>

<script>
import Header from "../components/Header";
import axios from "axios";
// import Choseevent from '../components/ChoseEvent'
import Signupinfo from "../components/signupinfo";
import Paymentinfo from "../components/Paymentinfo";
import Checksignupinfo from "../components/Checksignupinfo";
import Successpage from "../components/Successpage";
import Sidemenu from "../components/Sidemenu";
export default {
  data() {
    return {
      eventinfo: [],
      propscheckinfo: []
    };
  },
  created() {
    axios({
      method: "get",
      url:
        "https://script.google.com/macros/s/AKfycbzW4IbkVHdr_DCY3U65uBx4zV9cCmSBeU-FT1QsK_UbloDhniYPT-TGXQi5tky9M5UR/exec",
      params: {
        mode: "query",
        id: this.$route.params.id
      }
    }).then(response => {
      this.eventinfo = response.data[0];
      this.eventinfo[2] = this.convertdate(this.eventinfo[2]);
      this.$store.commit("setsignuplist", this.eventinfo[10]);
      this.$store.commit("setsignupinfo", this.eventinfo);
      this.$store.commit("seteventid", this.eventinfo[18]);
      console.log(response.data[0]);
    });
  },
  mounted() {
    if (window.sessionStorage.getItem("storehostid")) {
      var storehostid = window.sessionStorage.getItem("storehostid");
      this.$store.commit("sethostid", JSON.parse(storehostid));
    }

    // this.$store.commit('addsignupnumber')
  },
  methods: {
    convertdate(date) {
      let str = date;
      // console.log(str)
      let strlist = str.split("-");
      let result = (strlist[0] + "/" + strlist[1] + "/" + strlist[2]).substring(
        0,
        10
      );

      return result;
    }
  },
  computed: {
    signupprogressnum() {
      return this.$store.state.signupprogressnum;
    },
    showsidemenu() {
      return this.$store.state.showsidemenu;
    },
    maincolor(){
        return this.$store.state.maincolor;
    }
  },
  components: {
    Header,
    Signupinfo,
    Paymentinfo,
    Checksignupinfo,
    Successpage,
    Sidemenu
  }
};
</script>

