import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import Signup from "../views/Signup.vue";
import Profilepage from "../views/Profilepage.vue";
import Eventlistpage from "../views/EventListpage.vue";
import HistoryPage from "../components/Historypage.vue";
import SignupEventpage from "../components/Signupeventpage.vue";
import Userinfo from '../components/Profilecontent.vue'
import RecentEventlist from '../views/SendRecentEventinfo.vue'

Vue.use(VueRouter);

const routes = [
  {
    path: "/:hostid",
    name: "Home",
    component: Home,
  },
  {
    path: "/signup/:id",
    name: "Signup",
    component: Signup,
  },
  {
    path:"/recenteventlist/:hostid",
    name:"RecentEventlist",
    component:RecentEventlist
  },
  {
    path: "/eventlistpage/:hostid",
    name: "Eventlistpage",
    component: Eventlistpage,
  },
  {
    path: "/profilepage",
    name: "Profilepage",
    component: Profilepage,
    children: [
      {
        path:"userinfo/:hostid",
        name:"Userinfo",
        component:Userinfo
      },
      {
        path: "history/:hostid",
        name: "History",
        component: HistoryPage,
      },
      {
        path: "alreadysignup/:hostid",
        name: "alreadysignup",
        component: SignupEventpage,
      },
    ],
  },
  {
    path: "/eventpage/:id",
    name: "Eventpage",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Eventpage.vue"),
  },
];

const router = new VueRouter({
  routes,
});

export default router;
