<template>
<div class="signupinfoblock">
    <Inputoutline @emitcontent="catchcontent" :currentindex="index" style="marginTop:15px" v-for="(item,index) of signupinfolist" :key="index" :title="item" :alreadydata="checkinsertdata(item,index)"></Inputoutline>
</div>
</template>

<script>
import Inputoutline from '../components/Inputoutline'
export default {
    data() {
        return {
            signuplist: this.signupinfo,
            emitarr: []
        }
    },
    components: {
        Inputoutline
    },
    mounted() {
        // this.$store.commit('setpostsignupinfolength', this.signupinfolist.length)
    },
    methods: {
        catchcontent(val, index) {

            this.emitarr.length = this.signupinfolist.length;
            this.emitarr[index] = val;

        

            // let setdata = {
            //     index: index,
            //     value: val
            // }
            this.$emit('test', this.emitarr)
            // this.$store.commit('setpostsignupinfo', setdata)
            // this.$store.commit('seteventid', this.eventinfo[14])

        },
        convertdate(date) {
            let str = date
            // console.log(str)
            let strlist = str.split('-')
            let result = (strlist[0] + '/' + strlist[1] + '/' + strlist[2]).substring(0, 10)

            return result
        },
        checkinsertdata(datatype, index) {
            // let setdata = {
            //     index: index,
            //     value: this.userdata.displayName
            // }
            this.emitarr[index] = this.userdata.displayName;
            switch (datatype) {
                case '姓名':
                    // this.$store.commit('setpostsignupinfo', setdata)
                    return this.userdata.displayName;
            }

        }
    },
    computed: {
        signupinfolist() {
            return this.$store.state.singuplist;
        },
        eventinfo() {
            return this.$store.state.signupinfo
        },
        userdata() {
            return this.$store.state.userprofiledata;
        },
    }
}
</script>

