<template>
  <div class="actarea">
    <div class="actarea-title">
      <div  :style="{backgroundColor:maincolor}" class="decoline"></div>
      <h4>{{title}}</h4>
    </div>
    <h5 class="actarea-describe">{{describe}}</h5>

    <vuescroll :ops="ops">
      <div class="wrap" v-show="title=='推薦活動'">
        <ActBlock
         v-show="item[5]=='否'||item[5]==''"
          :pic="item[1]"
          :eventid="item[18]"
          :margin="margin"
          :name="item[0]"
          :loc="item[4]"
          :date="convertdate(item[2])"
          :time="item[3]"
          :number="item[7]"
          :deadlinedate="convertdate(item[8])"
          v-for="(item,index) of eventlist"
          :key="index"
          :fee="item[13]"
          :type="item[6]"
         
        ></ActBlock>
      </div>
      <div class="wrap" v-show="title=='近期活動'">
        <ActBlock
          :pic="item[1]"
          :eventid="item[18]"
          :name="item[0]"
          :loc="item[4]"
          :date="convertdate(item[2])"
          :time="item[3]"
          :number="item[7]"
          :deadlinedate="convertdate(item[8])"
          v-for="(item,index) of dateeventlist"
          :key="index"
          :fee="item[13]"
          :type="item[6]"
        ></ActBlock>
      </div>
    </vuescroll>
    <Outlinebtn width="1092px" height="53" btnname="更多活動"></Outlinebtn>

    <!-- <ActBlock></ActBlock>
    <ActBlock></ActBlock>-->
  </div>
</template>

<script>
import ActBlock from "../components/Actblock";
import Outlinebtn from "../components/outlinebtn";
import vuescroll from "vuescroll";
import axios from "axios";
export default {
  props: ["title", "describe",'margin'],
  data() {
    return {
      eventlist: [],
      dateeventlist: [],
     
      ops: {
        vuescroll: {
          mode: "native",
          sizeStrategy: "percent",
          detectResize: true,
          /** Enable locking to the main axis if user moves only slightly on one of them at start */
          locking: true
        },
        scrollPanel: {
          scrollingX: true,
          scrollingY: false,
          speed: 300,
          verticalNativeBarPos: "right"
        },
        bar: {
          showDelay: 500,

          keepShow: true,
          background: "#c1c1c1",
          opacity: 0,
          minSize: 0.2,
          size: "6px",
          disable: false
        }
      }
    };
  },
  created() {
    axios({
      method: "get",
      url:
        "https://script.google.com/macros/s/AKfycbzW4IbkVHdr_DCY3U65uBx4zV9cCmSBeU-FT1QsK_UbloDhniYPT-TGXQi5tky9M5UR/exec",
      params: {
        mode: "hostid",
        id: this.$route.params.hostid
      }
    }).then(response => {
      // console.log(response.data);
      if (this.title == "近期活動") {
        var today = new Date();
        let nowdays = new Date(
          today.getFullYear() +
            "/" +
            (today.getMonth() + 1) +
            "/" +
            today.getDate()
        );
        for (let i = 0; i < response.data.length; i++) {
          let actdate = new Date(response.data[i][2]);
          let minusdays = parseInt((nowdays - actdate) / 1000 / 60 / 60 / 24);
          // console.log(minusdays);
          if (minusdays > -30 && minusdays < 0) {
            this.dateeventlist.push(response.data[i]);
          }
        }
      }
      this.$emit("dataisloaded");
      this.eventlist = response.data;
    });
  },
  mounted() {},
  computed:{
    hostinfo() {
      return this.$store.state.hostinfo[0];
    },
    maincolor(){
      return this.$store.state.maincolor
    }
  },
  methods: {
    convertdate(date) {
      let str = date;
      // console.log(str)
      let strlist = str.split("-");
      let result = (strlist[0] + "/" + strlist[1] + "/" + strlist[2]).substring(
        0,
        10
      );

      return result;
    }
  },
  components: {
    ActBlock,
    vuescroll,
    Outlinebtn
  }
};
</script>

