<template>
  <div ref="searchfilter" class="searchfilter">
    <ul>
      <li
        ref="currentli"
        @click="openfilternav(index)"
        v-for="(item,index) in filterlinkgroup"
        :key="index"
      >{{item}}</li>
    </ul>
    <div class="acttype" v-show="currentindex==0">
      <span v-for="(item,index) of acttype" :key="index">{{item}}</span>
    </div>
    <div class="acttype" v-show="currentindex==2">
      <span v-for="(item,index) of time" :key="index">{{item}}</span>
    </div>
    <div class="acttype" v-show="currentindex==3">
      <span v-for="(item,index) of price" :key="index">{{item}}</span>
    </div>
  </div>
</template>

<script>
import anime from "animejs/lib/anime.es.js";
export default {
  data() {
    return {
      defaultcolor: "#4f4f4f",
      filterlinkgroup: ["活動分類", "銀髮大使", "時間", "價格"],
      open: false,
      acttype: ["戶外", "DIY", "共餐", "手作", "醫學", "熱門活動", "近期活動"],
      time: ["今天", "明天", "後天", "本週", "下週"],
      price: ["免費", "500以下", "1000以下", "1500以下"],
      currentindex: -1,
      styleobject: {
        color: this.maincolor
      },
      lastindex: -1
    };
  },
  computed: {
    hostinfo() {
      return this.$store.state.hostinfo[0];
    },
    maincolor() {
      return this.$store.state.maincolor;
    }
  },
  methods: {
    openfilternav(idx) {
      let searchfilter = this.$refs.searchfilter;

      if (this.open == false) {
        if (idx != 1) {
          anime({
            targets: searchfilter,
            height: "397px",
            easing: "easeInOutQuad",
            duration: 450
          });
          this.open = true;
        } else {
          console.log("test");
        }

        this.currentindex = idx;
        // this.lastindex = idx;
        // let currentli = this.$refs.currentli;
        // currentli[idx].style.color = this.maincolor;
      } else {
        if (this.currentindex != idx) {
          this.open = false;
          this.currentindex = idx;
          if (idx == 1) {
            anime({
              targets: searchfilter,
              height: "86px",
              easing: "easeInOutQuad",
              duration: 450
            });
            this.open = false;
            this.currentindex = idx;
          }
          // let currentli = this.$refs.currentli;
          // currentli[this.lastindex].style.color = this.maincolor;
        } else {
          anime({
            targets: searchfilter,
            height: "86px",
            easing: "easeInOutQuad",
            duration: 450
          });
          this.open = false;
          this.currentindex = -1;
        }

        // let currentli = this.$refs.currentli;
        // currentli[idx].style.color = "#4f4f4f";
      }
    }
  }
};
</script>


