<template>
  <div class="crwrap" >
    <div
      class="banner"
      v-for="(item,index) in test"
      :key="index"
      :style="{backgroundImage:'url('+item.url+')'}"
      alt
      v-show="currentindex==index"
    ></div>
  </div>
</template>

<script>
import anime from "animejs/lib/anime.es.js";
export default {
  data() {
    return {
      transitionname: "right-in",
      currentindex: 0,
      test: [
        {
          url:
            "https://drive.google.com/uc?id=1efg3vItB4qI0dKSYxgt1Sd10voH4PBsB"
        },
        {
          url:
            "https://drive.google.com/uc?id=1o5RBUQ4BHQRm7z3xnMUfZsGbdEOXyB_3"
        }
      ]
    };
  },
  mounted() {
     anime({
          targets: ".banner",
          // opacity: 1,
         
          scale:1.1,
          easing: "easeInOutQuad",
          loop:true,
          duration: 7000,
           direction: 'alternate',
        });
    setInterval(() => {
      this.addindex();
    }, 7000);
  },
  methods: {
    addindex() {
      this.currentindex++;

      if (this.currentindex == this.test.length) {
        this.currentindex = 0;
      }
    }
  }
};
</script>

