<template>
  <div class="wrap">
    <input
      type="text"
      ref="searchinput"
      :placeholder="placehold"
      class="searchbar"
      @keyup.enter="keywordhandler"
      v-model="searchkeyword"
      maxlength="37"
    />
    <i class="ri-search-2-line" @click="keywordhandler"></i>
  </div>
</template>

<script>
import "remixicon/fonts/remixicon.css";
export default {
  props: ["placehold", "width", "height"],
  data() {
    return {
      isclick: false,
      searchkeyword: "",
      
    };
  },
  mounted() {
    let maincolor = this.maincolor
    this.$refs.searchinput.addEventListener("focus", function() {
      this.style.border = 'solid 1px '+maincolor;
    });
    this.$refs.searchinput.addEventListener("blur", function() {
      this.style.border = "solid 1px #4f4f4f";
    });
  },
  methods: {
    keywordhandler() {
      this.$emit("emitkeyword", this.searchkeyword);
    }
  },computed:{
       hostinfo() {
      return this.$store.state.hostinfo[0];
    },
     maincolor(){
      return this.$store.state.maincolor
    }
  }
};
</script>

