<template>
<div class="successpage">

    <img class="checkicon" src="../assets/checkbox-circle-fill 1.svg" alt="">
    <span class="title">恭喜報名成功</span>
    <p class="describe">
        您已成功報名 {{eventname}} <br>
        我們會在活動前一天自動發送提醒
    </p>
    <fillbtn style="marginTop:15px" btnname="查看我的活動" width="130" height="35" @clickhandler="tomyevent"></fillbtn>

</div>
</template>

<script>
import fillbtn from '../components/fillbtn'
export default {
    data() {
        return {

        }
    },
    methods: {
        tomyevent() {
            this.$router.push({
                name: 'alreadysignup',
                params: {
                    hostid: this.hostid
                }
            })
            this.$store.commit('resetsignupinfo', 0)
        },
    },
    computed: {
        eventname() {
            return this.$store.state.signupinfo[0]
        },
        hostid() {
            return this.$store.state.hostid
        }
    },
    components: {
        fillbtn
    }
}
</script>

